.zone-banner {
	position: relative;
	overflow: hidden;
	background-image: url('/images/prospect/banner-clouds.jpg');
	background-size: cover;
}

.banner-video {
	position: absolute;
	top: 0; left: 50%;
	min-width: 100%;
	min-height: 100%;
	transform: translateX(-50%);
	z-index: 2;
}

.banner-content {
	position: absolute;
	top: 0; left: 0; right: 0; bottom: 0;
	background-color: rgba(0,0,0,0.25);
	z-index: 3;

	svg {
		height: 100vh;
		width: 100vw;
		opacity: 0.7;
	}
}