// cross zone variables
$header-nav-height: 5.125rem;
$unit-bg-gradients: linear-gradient(45deg,rgba($white,0.7),rgba($white,0)), linear-gradient(120deg, rgba(29,56,92,0) 60%, rgba(29,56,92,0.9) 100%), #295a9e;
$unit-bg-size: 100% 100vh;
$unit-bg-position: left top;


body {
  min-height: 100vh;
  min-height: -webkit-fill-available;
}

html {
  height: -webkit-fill-available;
}

// full screen zones
.zone-banner {
	height: 100vh;
	width: 100vw;
}

@include media-breakpoint-up(lg) {
	html { 
	  scroll-snap-type: y mandatory;
	}

	.zone-units,
	.zone-ci {
		height: 100vh;
		width: 100vw;
	}

	.zone-units,
	.zone-ci {
		scroll-snap-align: start;
		scroll-snap-stop: always;
	}

	.zone-banner,
	.footer-nav {
		scroll-snap-align: end;
	}
}
